import React, { useEffect } from "react";
import { Button, Box } from "@mui/material";
import { Google } from "@mui/icons-material";
import googleConfig from "../config/googleConfig";

const GoogleLoginButton = ({ onSuccess, onError }) => {
  useEffect(() => {
    // Load Google Identity Services script
    const script = document.createElement('script');
    script.src = 'https://accounts.google.com/gsi/client';
    script.async = true;
    script.defer = true;
    document.head.appendChild(script);

    script.onload = () => {
      if (window.google) {
        window.google.accounts.id.initialize({
          client_id: googleConfig.clientId,
          callback: handleCredentialResponse,
          auto_select: false,
          cancel_on_tap_outside: true,
        });
      }
    };

    return () => {
      // Cleanup script when component unmounts
      if (script.parentNode) {
        script.parentNode.removeChild(script);
      }
    };
  }, []);

  const handleCredentialResponse = (response) => {
    console.log("Google credential response:", response);
    if (response.credential) {
      // Decode the JWT token to get user info
      try {
        const base64Url = response.credential.split('.')[1];
        const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
        const jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
          return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
        }).join(''));
        
        const userInfo = JSON.parse(jsonPayload);
        console.log("Decoded user info:", userInfo);
        
        if (onSuccess) {
          onSuccess({
            credential: response.credential,
            user: userInfo
          });
        }
      } catch (error) {
        console.error("Error decoding Google credential:", error);
        if (onError) {
          onError(error);
        }
      }
    }
  };

  const handleGoogleLogin = () => {
    if (window.google && window.google.accounts) {
      window.google.accounts.id.prompt((notification) => {
        console.log("Google prompt notification:", notification);
        if (notification.isNotDisplayed() || notification.isSkippedMoment()) {
          // Fallback to popup if prompt doesn't work
          console.log("Prompt not displayed, trying popup...");
          
          // Create a more manual OAuth URL
          const authUrl = `https://accounts.google.com/oauth/authorize?` +
            `client_id=${googleConfig.clientId}&` +
            `redirect_uri=${encodeURIComponent(googleConfig.redirectUri)}&` +
            `scope=${encodeURIComponent(googleConfig.scope)}&` +
            `response_type=code&` +
            `access_type=offline`;
          
          // Open popup window
          const popup = window.open(
            authUrl,
            'google-oauth',
            'width=500,height=600,scrollbars=yes,resizable=yes'
          );
          
          // Monitor popup for completion
          const checkClosed = setInterval(() => {
            if (popup.closed) {
              clearInterval(checkClosed);
              console.log("Google OAuth popup closed");
            }
          }, 1000);
        }
      });
    } else {
      console.error("Google Identity Services not loaded");
      if (onError) {
        onError(new Error("Google Identity Services not loaded"));
      }
    }
  };

  return (
    <Button
      variant="contained"
      startIcon={<Google />}
      onClick={handleGoogleLogin}
      fullWidth
      sx={{
        backgroundColor: "#db4437",
        "&:hover": {
          backgroundColor: "#c23321",
        },
        padding: "12px",
        fontSize: "1rem",
        color: "white"
      }}
    >
      Continue with Google
    </Button>
  );
};

export default GoogleLoginButton;
